body {
  background-color: #d3d3d3;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.nav {
  justify-content: space-between;
  height: 60px;
  display: flex;
}

.container {
  background-color: #fff;
  border-radius: 10px;
  flex-direction: column;
  max-width: 300px;
  margin: auto;
  padding: 10px;
  display: flex;
}

.pet {
  background-color: #bc8f8f;
  width: 200px;
  height: 200px;
  margin: 10px auto;
}

.poop {
  width: 66px;
  height: 66px;
}

.btn-container {
  margin: 10px auto;
}

.btn-container button {
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 10px;
  margin: 5px;
  padding: 6px 10px;
  font-size: 1rem;
  box-shadow: 0 4px 10px #0000004d;
}

.modal {
  z-index: 1;
  background-color: #000000b3;
  justify-content: flex-end;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
}

.modal-content {
  background-color: #fff;
  border-radius: 10px;
  flex-direction: column;
  width: 100%;
  max-width: 200px;
  margin: auto;
  padding: 20px;
  display: flex;
  box-shadow: 0 4px 6px #0000001a;
}

.content-center {
  text-align: center;
  flex-direction: column;
  flex-grow: 1;
  display: flex;
}

.playAgainBtn {
  cursor: pointer;
  width: 50%;
  margin: auto;
}

.health-bar {
  background-color: #d3d3d3;
  width: 100%;
  height: 20px;
  position: relative;
}

.health-level {
  background-color: green;
  width: 100%;
  height: 100%;
  transition: width .3s;
}

.progress-bar {
  width: var(--width, 0%);
  background-color: #00f;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

