body {
  background-color: lightgray;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}

.nav {
  display: flex;
  justify-content: space-between;
  height: 60px;
}

.container {
  display: flex;
  flex-direction: column;
  max-width: 300px;
  margin: auto;
  background-color: white;
  padding: 10px;
  border-radius: 10px;
}

.pet {
  background-color: rosybrown;
  width: 200px;
  height: 200px;
  margin: 10px auto;
}

.poop {
  /* background-color: burlywood; */
  width: 66px;
  height: 66px;
}

.btn-container {
  margin: 10px auto;
}

.btn-container button {
  background-color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  padding: 6px 10px;
  margin: 5px;
  font-size: 1rem;
}

.modal {
  display: none;
  justify-content: flex-end;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(
    0,
    0,
    0,
    0.7
  ); /* Semi-transparent background overlay */
  z-index: 1;
}
.modal-content {
  background-color: white;
  padding: 20px;
  width: 100%;
  max-width: 200px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  margin: auto;
}

.content-center {
  text-align: center;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.playAgainBtn {
  width: 50%;
  margin: auto;
  cursor: pointer;
}

.health-bar {
  width: 100%;
  height: 20px;
  background-color: lightgray;
  position: relative;
}

.health-level {
  height: 100%;
  width: 100%; /* Initial width is 100% */
  background-color: green; /* Set the color for the health level */
  transition: width 0.3s; /* Add a transition for smooth width changes */
}

.progress-bar {
  position: absolute; /* Position the progress bar absolutely */
  top: 0;
  left: 0;
  height: 100%;
  width: var(--width, 0%); /* Initial width is 0% */
  background-color: blue; /* Set the color for the progress bar */
}
